import Prompt from '../../../components/zz-prompt/index.vue'
export default {
	components: {
		Prompt
	},
	data() {

		return {
			uuid: '',
			disData: [],
			refuseReason: '', //拒绝原因
			promptVisible: true, //弹出层
			butState: false, //操作按钮
		}
	},
	onLoad: function(option) {
		this.uuid = option.id
		this.dispatchDetailsQuery()
		console.log(this.uuid)
	},
	methods: {
		dispatchDetailsQuery() {
			let that=this
			//查询生产日志单详情
			this.$http.get(this.$store.state.producelogdetail, {
				uuid: this.uuid
			}).then(res => {
				console.log(res)
				this.disData = res.data.data

				if (res.data.data.auditStatus !== 'sh02') {
					that.butState = true
				}
			})
		},
		cancel() {
			//弹出框取消按钮
			this.promptVisible = true
		},
		clickPromptConfirm(val) {
			//审核驳回

			let data = {
				produceLogBeanList: [this.disData],
				auditStatus: 'sh03',
				refuseReason: val
			}
			let that = this
			if (val != '') {
				this.$http.post(this.$store.state.saveStateProducelog, data).then(res => {
					if (res.data.code == 200) {
						uni.showModal({
							title: '提示',
							content: '操作成功',
							showCancel: false,
							success(res) {
								that.promptVisible = true
								// that.dispatchDetailsQuery()
								if (res.confirm) {
									console.log('用户点击确定');
									uni.reLaunch({
										url: 'Home'
									});
								}
							}

						});
						this.butState = true
					}
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '请输入拒绝原因',
					showCancel: false
				});
			}


			console.log(data)
		},
		Refused() {
			//拒绝按钮
			this.promptVisible = false

		},
		accept() {
			let that = this
			//审核通过
			let data = {
				produceLogBeanList: [this.disData],
				auditStatus: 'sh01'
			}

			uni.showModal({
				title: '操作',
				content: '是否确认该操作？',
				showCancel: true,
				success(res) {
					if (res.confirm) {
						
						that.$http.post(that.$store.state.saveStateProducelog, data).then(res => {
							if (res.data.code == 200) {
								that.butState = true
								that.dispatchDetailsQuery()
								uni.showModal({
									title: '提示',
									content: '操作成功',
									showCancel: false,
									success(res) {

										if (res.confirm) {
											console.log('用户点击确定');
											uni.reLaunch({
												url: 'Home'
											});
										}
									}

								});
							}
						})
					}
				}

			});



		},

	}
}
